@import "../../core/variables";

$primary-button-text-color: $ui-blue-300 !default;
$primary-button-background-color: $ui-blue-600 !default;
$primary-button-hover-color: $ui-blue-200 !default;
$primary-button-active-color: $ui-blue-400 !default;

.lmr-primary-button {
  color: white;
  background-color: $primary-button-background-color;
  display: flex;
  border: none;
  border-radius: 2px;
  cursor: pointer;
  justify-content: center;
  padding: 6px 16px;
  font-weight: bold;
  line-height: 20px;
  font-size: 14px;
  &:focus {
    outline: none;
  }

  &:hover {
    background-color: $primary-button-hover-color;
    transition: background-color 0.2s linear;
  }

  &:active {
    background-color: $primary-button-active-color;
  }
}


@media screen and (max-width: 768px) {
  .lmr-primary-button {
    color: white;
    background-color: $primary-button-background-color;
    display: flex;
    border: none;
    border-radius: 2px;
    cursor: pointer;
    justify-content: center;
    padding: 6px 16px;
    font-weight: bold;
    line-height: 20px;
    font-size: 2.8vw;
    &:focus {
      outline: none;
    }

    &:hover {
      background-color: $primary-button-hover-color;
      transition: background-color 0.2s linear;
    }

    &:active {
      background-color: $primary-button-active-color;
    }
  }

}
