@import "variables";

@mixin stroke($color: #000, $size: 1px) {
  text-shadow: -#{$size} -#{$size} 0 $color,
  0        -#{$size} 0 $color,
  #{$size} -#{$size} 0 $color,
  #{$size}  0 0 $color,
  #{$size}  #{$size} 0 $color,
  0         #{$size} 0 $color,
  -#{$size}  #{$size} 0 $color,
  -#{$size} 0 0 $color;
}

.lmr-title-text-with-outline {
  font-family: 'Roboto', serif;
  font-weight: 900;
  font-style: normal;
  font-size: 3.4vw;
  line-height: 3.3vw;
  align-self: center;
  alignment: center;
  color: $ui-blue-300;
  letter-spacing: 1px;
  @include stroke($color: #FFF, $size: 1.4px);
}

.lmr-title-text-medium {
  font-family: 'Roboto', serif;
  font-weight: 400;
  font-style: normal;
  font-size: 28px;
  line-height: 33px;
  align-self: center;
  alignment: center;
  color: $ui-blue-500;
}

.lmr-title-text-small {
  font-family: 'Roboto', serif;
  font-weight: 700;
  font-style: normal;
  font-size: 20px;
  line-height: 25.3px;
  align-self: center;
  alignment: center;
  color: $ui-blue-600;
}

.lmr-logo-text {
  font-family: 'Roboto', serif;
  font-weight: 900;
  font-style: normal;
  font-size: 4.8vh;
  line-height: 4.3vw;
  align-self: center;
  alignment: center;
  color: $ui-blue-300;
  z-index: 0;
}

.lmr-georgia-text-xlarge {
  font-family: Georgia, Times, "Times New Roman", serif;
  font-weight: 700;
  font-style: normal;
  font-size: 48px;
  line-height: 20px;
  align-self: center;
  alignment: center;
  color: white;
}

.lmr-georgia-text-large {
  font-family: Georgia, Times, "Times New Roman", serif;
  font-weight: 400;
  font-style: normal;
  font-size: 20px;
  line-height: 25px;
  align-self: center;
  alignment: center;
  color: $ui-blue-600;
}

.lmr-georgia-text-medium {
  font-family: Georgia, Times, "Times New Roman", serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 19px;
  align-self: center;
  alignment: center;
  color: black;
